* {
    padding:0;
    margin:0;
    font-family:Helvetica;
}

.outter-layout { 
    padding:20px;
    /* background-color:var(--primary); */
}

.description  {
    padding:10px 0;
}
.inner-desc { 
    display:flex;
    flex-direction : row;
    color:white;
}
.inner-desc > .key-desc {
    min-width:30%;
}

.key-desc { 
    padding-right:5px;
}

.desc-header { 
    font-size : 16px;
    color:white;
    padding:8px 0 ;
}

img {
    width:100%;
    object-fit:cover;
}

.panel {
    /* background-color:#3E4245; */
    background-color:#6E757A;
    border-radius:10px;
      /* grid-row: 1 / -1; */
      /* grid-column: 1; */
}

.breeds { 
    display:grid;
    grid-gap :12px;
    grid-template-columns : repeat(auto-fill,minmax(380px,1fr));

}
