* {
    margin : 0 ;
    padding : 0;
}

.panel { 
    font-size:12px;
    font-family:Helvetica;
    background-color:gray;
    display:flex;
    flex-direction:column;
    padding:20px;
}

.panel .text{
    text-align:left;
}
.panel-group{ 
    display:grid;
      grid-template-columns: repeat(2, 1fr);
      gap: 20px;
      /* grid-template-rows : 200px; */
      grid-auto-rows: minmax(200px, auto);
    /* width: 200px; */
}

.inner-image { 
    max-width: 100%;
}

.li { 
    text-decoration : none;
}
